'use client';

import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { useRouter } from 'next/navigation';

// نوع بيانات المستخدم
export interface User {
  id: string;
  name: string;
  email: string;
  role: 'admin' | 'provider' | 'client';
  avatar?: string;
  isVerified: boolean;
}

// نوع بيانات سياق المصادقة
interface AuthContextType {
  user: User | null;
  loading: boolean;
  error: string | null;
  login: (email: string, password: string) => Promise<boolean>;
  googleLogin: () => Promise<boolean>;
  signup: (name: string, email: string, password: string, role: 'provider' | 'client') => Promise<boolean>;
  logout: () => void;
  isAuthenticated: boolean;
  isAdmin: boolean;
  isProvider: boolean;
  isClient: boolean;
}

// إنشاء سياق المصادقة
const AuthContext = createContext<AuthContextType | undefined>(undefined);

// مزود سياق المصادقة
export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const router = useRouter();

  // التحقق من حالة المصادقة عند تحميل الصفحة
  useEffect(() => {
    // محاكاة التحقق من المصادقة
    const checkAuth = async () => {
      try {
        // في الإنتاج، هنا يتم إرسال طلب إلى API للتحقق من الجلسة
        const storedUser = localStorage.getItem('user');
        
        if (storedUser) {
          setUser(JSON.parse(storedUser));
        }
      } catch (err) {
        console.error('Error checking authentication:', err);
      } finally {
        setLoading(false);
      }
    };

    checkAuth();
  }, []);

  // وظيفة تسجيل الدخول
  const login = async (email: string, password: string): Promise<boolean> => {
    setLoading(true);
    setError(null);
    
    try {
      // محاكاة عملية تسجيل الدخول (في الإنتاج، هنا يتم إرسال طلب إلى API)
      await new Promise(resolve => setTimeout(resolve, 800)); // تأخير لمحاكاة الشبكة
      
      // تحديد نوع المستخدم بناءً على البريد الإلكتروني (للعرض التوضيحي فقط)
      let role: 'admin' | 'provider' | 'client' = 'client';
      
      if (email.includes('admin')) {
        role = 'admin';
      } else if (email.includes('provider')) {
        role = 'provider';
      }
      
      const userData: User = {
        id: Math.random().toString(36).substring(2),
        name: email.split('@')[0],
        email,
        role,
        isVerified: true,
        avatar: role === 'admin' 
          ? '/images/avatars/admin-avatar.jpg' 
          : role === 'provider' 
            ? '/images/avatars/provider-avatar.jpg' 
            : '/images/avatars/client-avatar.jpg'
      };
      
      // حفظ بيانات المستخدم
      setUser(userData);
      localStorage.setItem('user', JSON.stringify(userData));
      
      return true;
    } catch (err) {
      setError('فشل تسجيل الدخول. يرجى التحقق من بيانات الاعتماد الخاصة بك.');
      return false;
    } finally {
      setLoading(false);
    }
  };

  // وظيفة تسجيل الدخول باستخدام جوجل
  const googleLogin = async (): Promise<boolean> => {
    setLoading(true);
    setError(null);
    
    try {
      // محاكاة عملية تسجيل الدخول بواسطة جوجل (في الإنتاج، هنا يتم التفاعل مع API جوجل)
      await new Promise(resolve => setTimeout(resolve, 1000)); // تأخير لمحاكاة الشبكة
      
      const userData: User = {
        id: 'google-' + Math.random().toString(36).substring(2),
        name: 'مستخدم جوجل',
        email: 'user@gmail.com',
        role: 'client', // افتراضياً المستخدمين الجدد من جوجل هم عملاء
        isVerified: true,
        avatar: '/images/avatars/google-avatar.jpg'
      };
      
      // حفظ بيانات المستخدم
      setUser(userData);
      localStorage.setItem('user', JSON.stringify(userData));
      
      return true;
    } catch (err) {
      setError('فشل تسجيل الدخول باستخدام جوجل. يرجى المحاولة مرة أخرى.');
      return false;
    } finally {
      setLoading(false);
    }
  };

  // وظيفة إنشاء حساب جديد
  const signup = async (
    name: string, 
    email: string, 
    password: string, 
    role: 'provider' | 'client'
  ): Promise<boolean> => {
    setLoading(true);
    setError(null);
    
    try {
      // محاكاة عملية إنشاء حساب (في الإنتاج، هنا يتم إرسال طلب إلى API)
      await new Promise(resolve => setTimeout(resolve, 1000)); // تأخير لمحاكاة الشبكة
      
      const userData: User = {
        id: Math.random().toString(36).substring(2),
        name,
        email,
        role,
        isVerified: false,
        avatar: role === 'provider' 
          ? '/images/avatars/provider-avatar.jpg' 
          : '/images/avatars/client-avatar.jpg'
      };
      
      // في الإنتاج لا نقوم بتسجيل الدخول تلقائياً، بل ننتظر تأكيد البريد الإلكتروني
      // لكن هنا سنتخطى هذه الخطوة للعرض التوضيحي
      
      // حفظ بيانات المستخدم
      setUser(userData);
      localStorage.setItem('user', JSON.stringify(userData));
      
      return true;
    } catch (err) {
      setError('فشل إنشاء الحساب. يرجى المحاولة مرة أخرى.');
      return false;
    } finally {
      setLoading(false);
    }
  };

  // وظيفة تسجيل الخروج
  const logout = () => {
    setUser(null);
    localStorage.removeItem('user');
    router.push('/');
  };

  const value = {
    user,
    loading,
    error,
    login,
    googleLogin,
    signup,
    logout,
    isAuthenticated: !!user,
    isAdmin: user?.role === 'admin',
    isProvider: user?.role === 'provider',
    isClient: user?.role === 'client',
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}

// هوك استخدام سياق المصادقة
export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
} 